body {
	font-family: 'Open Sans', sans-serif;
	margin: 0;
}

.card {
	border: 1px solid #969696;
	box-shadow: 0 2px 4px grey;
	min-width: 350px;
	max-height: calc(100vh - 2rem);
}

.card-title {
	background-color: #29434e;
	color: #fff;
	font-size: 1.5rem;
	margin: 0;
	padding: .5rem .5rem;
}

.normal-btn {
	background-color: #1075c7;
	border: 1px solid transparent;
	border-radius: 5px;
	color: #fff;
	cursor: pointer;
	font-size: .85rem;
	min-width: 26px;
	padding: .3rem;
	margin: 0 0.3rem;
}

.normal-btn:disabled {
	background-color: #0a4576;
	color: grey;
	cursor: default;
}
.normal-btn:hover:enabled {
	background-color: #4aa6f0;
}

.small-btn {
	background-color: inherit;
	border: 1px solid transparent;
	color: grey;
	cursor: pointer;
	padding: 0;
	margin-left: 0.2rem;
}

.small-btn:hover {
	background-color: inherit;
	color: #000;
}

.normal-btn:focus,
.small-btn:focus {
	outline: none;
}

.monospace {
	font-family: 'Roboto Mono', monospace;
	font-size: .9rem;
}

.center {
	text-align: center;
}

.tab-list {
	font-size: 1.1rem;
	border-bottom: 1px solid #b3b3b3;
	padding: 0.25rem 0 0 0;
	margin: 0;
	background-color: #cccccc;
}

.tab-list-item {
	cursor: pointer;
	display: inline-block;
	list-style: none;
	margin-bottom: -1px;
	margin-left: 0.25rem;
	padding: 0.5rem 2rem;
	font-weight: normal;
}

.tab-list-active {
	background-color: white;
	border: solid #b3b3b3;
	border-width: 1px 1px 0 1px;
	font-weight: bold;
}

.colors-table {
	border-collapse: collapse;
	cursor: pointer;
	text-align: left;
}

.colors-table th,
.colors-table td {
	border-bottom: 1px solid #cccccc;
	padding: .4rem;
}

.colors-table > tbody > tr:hover {
	background-color: #d2d2d2;
}

.colors-table td {
	font-family: 'Roboto Mono', monospace;
}

hr {
	/* margin: 0.5rem 0.25rem; */
	margin: 0;
	border-top: 1px solid #cccccc;
}

.color-filter:hover {
	background-color: #d2d2d2;
}

.color-filter-item {
	font-size: 0.9rem;
	padding: 0.25rem 0.5rem;
	cursor: pointer;
}

.color-filter-active {
	background-color: #4dd2ff;
}

.color-filter-item:hover:not(.color-filter-active) {
	background-color: #cccccc;
}

::-webkit-scrollbar {
	width: 0.5rem;
}

::-webkit-scrollbar-thumb {
	background: #718792;
}

::-webkit-scrollbar-thumb:hover {
	background: #4e5d65;
}

::-webkit-scrollbar-track {
	background: white;
}

.sidebar-header {
	font-size: 0.9rem;
	text-align: center;
	letter-spacing: 1px;
	color: #666666;
	/* padding: 0.5rem 0.5rem 0 0.5rem; */
	/* margin-top: 0.5rem; */
}

.sidebar {
	border-left: 1px solid #cccccc;
	background-color: whitesmoke;
}

